import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

interface FormDialogueprops{
  setUname:(a:string)=>void,
}

export default function FormDialog({setUname}:FormDialogueprops) {
  const [open, setOpen] = React.useState(false);
  let uname=''

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    localStorage.setItem('uname', uname);
    setOpen(false);
    setUname(uname)
    console.log(uname)
  };
  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) =>{
    uname = e.target.value
  }

  return (
    <div className='wel-text'>
      <Button variant="outlined" color="primary"  onClick={handleClickOpen}>
        Set User Id
      </Button>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Set User Id</DialogTitle>
        <DialogContent>
          <DialogContentText>
            If you are a new user please set your UserId. You will later use it to view your todos.
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="Enter your Userid"
            type="string"
            fullWidth
            onChange={handleChange} 
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleClose} color="primary">
            Set
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
